export default{
    data(){
        return{
            height:0,
            width:0,
            loading:false,
            img:"",
            scanning:0,
        }
    },
    methods: {
        startFaceMesh(){
          let that=this;
          CameraPreview.takeSnapshot({quality: 10}, async function(base64PictureData){
            that.img="data:image/jpeg;base64," + base64PictureData;
              // const image = new Image();
              // image.src = "data:image/jpeg;base64," + base64PictureData;
              // image.onload = async function() {
                  // that.startScan(image);
              // }
          });
        },
        async startScan(videoEl){
            let that=this;
            var video=videoEl;
        },
    },
    async mounted() {
        let that=this;
        if (typeof cordova == "object") {
            document.addEventListener("deviceready", function() {
                that.width=window.screen.width;
                that.height=window.screen.height;
                let options = {
                    // x: 10,
                    // y: 150,
                    width: that.width,
                    height: that.height,
                    camera: that.$store.state.camera=='belakang' ? CameraPreview.CAMERA_DIRECTION.BACK:CameraPreview.CAMERA_DIRECTION.FRONT,
                    toBack: true,
                    tapPhoto: false,
                    tapFocus: false,
                    previewDrag: false,
                    storeToFile: false,
                    disableExifHeaderStripping: true
                };
                CameraPreview.startCamera(options);
                CameraPreview.onBackButton(function() {
                    CameraPreview.stopCamera();
                });
                setTimeout(() => {
                    that.startFaceMesh();
                }, 1000);
                // that.startVideo();
                that.$forceUpdate();
            });
        }
    },
}
</script>
